<template>
    <div class="login-page default-page">
        <header v-if="isShowHeader">
            <van-nav-bar
                    title="Sign in"
                    fixed
                    placeholder
            ></van-nav-bar>
        </header>
        <section class="login-page-body">
			<h2 class="vip-doc-block__title">Welcome</h2>
            <div ref="login_form">
                <van-field
                        readonly
                        name="picker"
                        :value="countryCodeText"
                        label="Country"
                        placeholder="Please select country code"
                />
				<!--
                <van-popup v-model="showPicker" position="bottom">
                    <van-picker
                            show-toolbar
                            :columns="columns"
                            @confirm="onConfirm"
                            @cancel="showPicker = false"
                    />
                </van-popup>-->
                <van-field
                        v-model="telephone"
                        name="telephone"
						type="tel"
                        label="Telephone"
                        placeholder="Please enter telephone"
						@blur="checkIsNeedConfirmPassword"
						:rules="[{ validator, message: 'Only numbers allowed' }]"
                />
				<van-field
						v-model="password"
						name="password"
						type="password"
						label="Password"
						placeholder="Please enter password"
				/>
				<van-field
						v-if="isNeedConfirmPassword"
						v-model="confirm_password"
						name="confirm_password"
						type="password"
						label="Confirm Password"
						placeholder="Please confirm password"
				/>
				<van-field name="checkbox">
					<template #input>
						<van-checkbox v-model="checkbox" checked-color="#07c160" >
							I have read and agreed to the <span class="red" @click.stop="toTermsPage">Terms & Conditions</span>
						</van-checkbox>
					</template>
				</van-field>

                <div class="vip-btn-wrap">
                    <van-button type="primary" class="vip-btn"
								:disabled="isSubmitting"
								:loading="isSubmitting"
								loading-text="Submitting..."
								@click="onSubmit"
                                block>Login / Registration</van-button>
					<div class="login-tips text-right">
						<p>Unregistered users will automatically create accounts</p>
					</div>
					<div class="login-forgot-password text-right">
						<a href="javascript:;" @click.prevent="toResetPasswordPage">Forgot your password?</a>
					</div>
                </div>
            </div>
        </section>

    </div>
</template>
<script>
	import { getCountryCodes,getVerificationCodes,queryPwd } from '@/api/user'
    export default {
        data(){
            return {
				countryCodeText: 'India 0091',
                countryCode: '0091',

                columns: [],
                showPicker: false,

                telephone: '',//9829666666
				password: '',
				confirm_password: '',
				isNeedConfirmPassword: true,
                code: '',
                codekey: '',
				checkbox: true,
                isGetting: false,
                gettingText: '',
                count: 60,
                COUNT_DEFAULT: 60,
                isSubmitting: false,
            }
        },
		created(){
			/*getCountryCodes().then((res)=>{
				const list = res.data || [];
				list.sort((item1,item2)=>{
					if(item1.is_hot < item2.is_hot){
						return 1;
					}else if(item1.is_hot > item2.is_hot){
						return -1;
					}else{
						return 0;
					}
				});
				list.forEach((item)=>{
					item.text = item.name +' '+ item.intl_code;
				});
				this.columns = list;
			});*/
		},
		computed: {
			isShowHeader() {
				const route = this.$route
				const { meta } = route
				return meta.showHeader
			},
		},
        methods: {
            onConfirm(value) {

                this.countryCode = value.intl_code;
                this.countryCodeText = value.text;

                this.showPicker = false;
            },
            onSubmit(){
				if(!this.telephone){
					this.$toast('Please enter telephone');
					return false;
				}
				if(!this.password){
					this.$toast('Please enter password');
					return false;
				}
				if(this.isNeedConfirmPassword){

					if(!this.confirm_password){
						this.$toast('Please confirm password');
						return false;
					}

					if(this.password != this.confirm_password){
						this.$toast("The password doesn't match.");
						return false;
					}
				}

				this.isSubmitting = true;
				let params = {
					device_number: this.$store.getters.deviceNumber,
					platform: this.$store.getters.platform,
					app_version_id: this.$store.getters.appVersionId,

					phone_number: this.telephone,
					password: this.password,
				}
				if(this.isNeedConfirmPassword){
					params.confirm_password = this.confirm_password
				}

				this.$store.dispatch('user/login', params)
					.then((res) => {
						const { data } = res;
						this.isSubmitting = false;
						if(data){
							this.toNextPage();
						}
					})
					.catch(() => {
						this.isSubmitting = false
					})

            },
			toNextPage(){
				this.$store.dispatch('user/getInfo').then(data=>{
					const { name,is_member } = data;
					if(name){
						if(is_member){
							this.toEndPage()
						}else{
							this.toVipPage()
						}

					}else {
						this.toInfoPage()
					}
				});
			},
			toEndPage(){
				this.$router.push({name:'end'});
			},
			toInfoPage(){
				this.$router.push({name:'info'});
			},
			toVipPage(){
				this.$router.replace({
					name:'vip',
				});
			},
			toCodePage(){
				this.$router.push({
					name:'code',
					params:{
						telephone:this.telephone,
						countryCode:this.countryCode
					}
				});
			},
			toTermsPage(){
				this.$router.push({name:'terms'});
			},
			// У�麯������ true ��ʾУ��ͨ����false ��ʾ��ͨ��
			validator(val) {
				return /^[0-9]*$/.test(val);
			},
			checkIsNeedConfirmPassword(){
				if(this.telephone){
					this.queryPassword();
				}
			},
			queryPassword(){
				return new Promise((resolve, reject) => {
					let params = {
						phone_number: this.telephone
					}
					queryPwd(params).then(res=>{
						let data = res.data;
						this.isNeedConfirmPassword = data.has_pwd == 'Y' ? false : true;
					});
				})

			},
			toResetPasswordPage(){
				this.$router.push({
					name:'resetPassword',
				});
			},
        }
    }
</script>
